import React, { useContext } from "react";
import styled from "styled-components";
import { HeroContext } from "../contexts/HeroContext";
import { Link } from "react-router-dom";
import wallpaper from "../assets/images/wallpaper.jpg";
import powgif from "../assets/animation/gif/powgif.gif";

const Container = styled.section`
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
`;

const ContainerLayout = styled.section`
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: center;
  @media (max-width: 850px) {
    flex-wrap: wrap-reverse;
  }

  @media (max-width: 750px) {
    flex-wrap: wrap-reverse;
  }
`;

const Title = styled.h1`
  font-size: 40px;
`;

const Paragraph = styled.p`
  font-size: 20px;
  padding: 10px;
`;

const Image = styled.img`
  width: 100%;
  height: 100%;
`;

const ImageContainer = styled.div`
  width: 90vw;
  height: 88vh;

  @media (max-width:850px){ 
    width: 100vw;
  height: 88vh;
  @media (max-width:750px){ 
  display:none;
`;

const Button = styled.button`
  padding: 25px 10px;
  border-radius: 6px;
  text-align: center;
  font-weight: bold;
  max-width: 320px;
  height: 60px;
  margin-bottom: 60px;

  color: rgba(255, 255, 255, 1);
  cursor: pointer;
  letter-spacing: 2px;
  box-shadow: inset 0 0 0 1px rgba(0, 0, 0, 0.1);

  transition: all ease 0.5s;
  background: #10b87b;
`;

const Home = () => {
  const { heroes } = useContext(HeroContext);
  return (
    <ContainerLayout>
      <ImageContainer>
        <Image src={wallpaper} />
      </ImageContainer>

      <Container>
        <Title>Hero App</Title>

        {heroes.length > 0 ? (
          <Container>
            <Paragraph>
              Vemos que ya agregaste algunos personajes puedes ver tu eupio
              haciendo click en el siguiente boton"
            </Paragraph>
            <Link to="/MyTeam">
              <Button>Ver equipo</Button>
            </Link>
          </Container>
        ) : (
          <Container>
            <Paragraph>
              Para crear tu equipo va s tener que elegir 6 personajes , ademas
              vas a poder ver sus Stats para decidir si quieres agregarlo a tu
              equipo o no
            </Paragraph>
            <Link to="/Search">
              <Button>Comenzar</Button>
            </Link>
          </Container>
          
        )}
      </Container>
    </ContainerLayout>
  );
};

export default Home;
